const quizQuestions =[{"question":"Where was the first \"sit-in\" at a segregated lunch counter?","answers":[{"content":"Greensboro, North Carolina","type":true},{"content":"Jackson, Mississippi","type":false},{"content":"Charleston, South Carolina","type":false}],"postAnswer":"The Greensboro sit-ins were a series of nonviolent protests in February to July 1960, primarily in the Woolworth store, now the International Civil Rights Center and Museum, in Greensboro, North Carolina, which led to the F. W. Woolworth Company department store chain removing its policy of racial segregation in the Southern United States.","wikiLink":"https://en.wikipedia.org/wiki/Greensboro_sit-ins"},{"question":"Which Supreme Court case ruled that the segregation of public schools was unconstitutional?","answers":[{"content":"Brown v. Board of Education","type":true},{"content":"Plessy v. Ferguson","type":false},{"content":"Dred Scott v. Sandford","type":false}],"postAnswer":"Brown v. Board of Education of Topeka, was a landmark decision of the U.S. Supreme Court in which the Court ruled that U.S. state laws establishing racial segregation in public schools are unconstitutional, even if the segregated schools are otherwise equal in quality. Handed down on May 17, 1954, the Court's unanimous (9–0) decision stated that \"separate educational facilities are inherently unequal,\" and therefore violate the Equal Protection Clause of the Fourteenth Amendment of the U.S. Constitution. However, the decision's 14 pages did not spell out any sort of method for ending racial segregation in schools, and the Court's second decision in Brown II in 1955 only ordered states to desegregate \"with all deliberate speed.\"","wikiLink":"https://en.wikipedia.org/wiki/Brown_v._Board_of_Education"},{"question":"What was the outcome of the murder trial of Emmet Till?","answers":[{"content":"The two defends were acquitted by an all white jury","type":true},{"content":"The two defendents were given life in prison","type":false},{"content":"The two defendents were given the death sentence","type":false}],"postAnswer":"In September 1955, an all-white jury found Bryant and Milam not guilty of Till's kidnapping and murder. Protected against double jeopardy, the two men publicly admitted in a 1956 interview with Look magazine that they had killed Till. Till's murder was seen as a catalyst for the next phase of the civil rights movement.","wikiLink":"https://en.wikipedia.org/wiki/Emmett_Till"},{"question":"What event was the catalyst for the Montgomery Bus Boycott?","answers":[{"content":"Claudette Colvin and Rosa Parks refused to give up their seats to a white person on a public bus","type":true},{"content":"A segregated bus system was established in Montgomery, Alabama","type":false},{"content":"Bus fairs were raised only for Black passengers","type":false}],"postAnswer":"On December 1, 1955, nine months after a 15-year-old high school student, Claudette Colvin, refused to give up her seat to a white passenger on a public bus in Montgomery, Alabama, and was arrested, Rosa Parks did the same thing. Parks soon became the symbol of the resulting Montgomery Bus Boycott and received national publicity. She was later hailed as the \"mother of the civil rights movement\".","wikiLink":"https://en.wikipedia.org/wiki/Montgomery_Bus_Boycott"},{"question":"How did Arkansas Governor Orval Faubus respond to the effort of the nine African Americans to attend Little Rock High School in 1957?","answers":[{"content":"He asked the president to intervene and block the school’s integration","type":true},{"content":"He urged the school to treat the students fairly","type":false},{"content":"He called out the National Guard to block their way","type":false}],"postAnswer":"A crisis erupted in Little Rock, Arkansas, when Governor of Arkansas Orval Faubus called out the National Guard on September 4 to prevent entry to the nine African-American students who had sued for the right to attend an integrated school, Little Rock Central High School.[60] Under the guidance of Daisy Bates, the nine students had been chosen to attend Central High because of their excellent grades.","wikiLink":"https://en.wikipedia.org/wiki/Little_Rock_Nine"},{"question":"Which Civil Rights organization sent out youth field organizers to lead sit-ins, encourage Black voter registration, and challenge civil segregation?","answers":[{"content":"Student Nonviolent Coordinating Committee","type":true},{"content":"Fellowship of Reconciliation","type":false},{"content":"Congress of Racial Equality","type":false}],"postAnswer":"The Student Nonviolent Coordinating Committee was the principal channel of student commitment in the United States to the Civil Rights Movement during the 1960s. Emerging in 1960 from the student-led sit-ins at segregated lunch counters in Greensboro, North Carolina and Nashville, Tennessee, the Committee sought to coordinate and assist direct-action challenges to the civic segregation and political exclusion of African-Americans.","wikiLink":"https://en.wikipedia.org/wiki/Student_Nonviolent_Coordinating_Committee"},{"question":"Which organization challenged the non-enforcement of the Supreme Court's ruling that \"segregated public buses were unconstitutional?\"","answers":[{"content":"Freedom Riders","type":true},{"content":"Students for a Democratic Society","type":false},{"content":"Prayer Pilgrimage","type":false}],"postAnswer":"The Freedom Riders were inspired by the 1947 Journey of Reconciliation, led by Bayard Rustin and George Houser and co-sponsored by the Fellowship of Reconciliation and the then-fledgling Congress of Racial Equality (CORE). Like the Freedom Rides of 1961, the Journey of Reconciliation was intended to test an earlier Supreme Court ruling that banned racial discrimination in interstate travel. The first Freedom Ride began on May 4, 1961 and included seven black and six white riders.","wikiLink":"https://en.wikipedia.org/wiki/Freedom_Riders"},{"question":"Founded by representatives of SNCC, CORE, and the NAACP in 1962, which organization led an effort to register Black voters throughout Mississippi?","answers":[{"content":"Council of Federated Organizations","type":true},{"content":"Voter Education Project","type":false},{"content":"Mississippi Freedom Democratic Party","type":false}],"postAnswer":"The Council of Federated Organizations (COFO) was a coalition of the major Civil Rights Movement organizations operating in Mississippi. COFO was formed in 1961 to coordinate and unite voter registration and other civil rights activities in the state and oversee the distribution of funds from the Voter Education Project. It was instrumental in forming the Mississippi Freedom Democratic Party.","wikiLink":"https://en.wikipedia.org/wiki/Council_of_Federated_Organizations"},{"question":"Propelled by their previously lackluster response to the civil rights activism in the early 1960s, the Kennedy administration federally endorsed which organizing effort?","answers":[{"content":"The Voter Education Project","type":true},{"content":"The Civil Liberties Project","type":false},{"content":"Voting Rights Act of 1965","type":false}],"postAnswer":"Starting in 1960, the explosion of student-led activism of sit-ins, and Freedom Rides during the Civil Rights Movement created a public relations and foreign policy embarrassment for the Kennedy administration. Voter Education Project (VEP) raised and distributed foundation funds to civil rights organizations for voter education and registration work in the southern United States from 1962 to 1992. The project was federally endorsed by the Kennedy administration in hopes that the organizations of the ongoing Civil Rights Movement would shift their focus away from demonstrations and more towards the support of voter registration.","wikiLink":"https://en.wikipedia.org/wiki/Voter_Education_Project"},{"question":"After applying and being rejected from Mississippi Southern College, this Black Korean War-veteran published a series of letters about the need for desegregation and was imprisoned by the state of Mississippi on false charges","answers":[{"content":"Clyde Kennard","type":true},{"content":"Dick Gregory","type":false},{"content":"Medgar Evers","type":false}],"postAnswer":"In the 1950s, Clyde Kennard attempted several times to enroll at the all-white Mississippi Southern College (now the University of Southern Mississippi) to complete his undergraduate degree started at the University of Chicago. Although the United States Supreme Court had ruled in 1954 that segregation of public schools was unconstitutional, USM rejected him. After Kennard published a letter in the local paper about integrated education, the Mississippi State Sovereignty Commission, a state-supported agency, conspired to have him arrested on false charges.","wikiLink":"https://en.wikipedia.org/wiki/Clyde_Kennard"},{"question":"Which coalition was active in Southwest Georgia and sought to draw attention to brutally enforced racial segregation practices in the area?","answers":[{"content":"Albany Movement","type":true},{"content":"Georgia Coalition","type":false},{"content":"Criterion Club","type":false}],"postAnswer":"The Albany Movement was a desegregation and voters' rights coalition formed in Albany, Georgia, in November 1961. This movement was founded by local black leaders and ministers, as well as members of the Student Nonviolent Coordinating Committee (SNCC) and the National Association for the Advancement of Colored People (NAACP). This group has been assisted by Martin Luther King Jr. and the Southern Christian Leadership Conference(SCLS).","wikiLink":"https://en.wikipedia.org/wiki/Albany_Movement"},{"question":"Built off of lessons learned during the Albany Movement, this effort garnered international attention and served as a model of nonviolent direct action protest","answers":[{"content":"Birmingham Campaign","type":true},{"content":"Southern Protest Movement","type":false},{"content":"Mississippi Movement","type":false}],"postAnswer":"The Birmingham campaign, or Birmingham movement, was a movement organized in early 1963 by the Southern Christian Leadership Conference (SCLC) to bring attention to the integration efforts of African Americans in Birmingham, Alabama. Led by Martin Luther King Jr., James Bevel, Fred Shuttlesworth and others, the campaign of nonviolent direct action culminated in widely publicized confrontations between young black students and white civic authorities, and eventually led the municipal government to change the city's discrimination laws.","wikiLink":"https://en.wikipedia.org/wiki/Birmingham_campaign"},{"question":"The brainchild of SCLC officer James Bevel, this bold and controversial strategy used during the Birmingham Campaign led to direct intervention by JFK.","answers":[{"content":"Children's Crusade","type":true},{"content":"Letter from Birmingham Jail","type":false},{"content":"Lunch Counter Sit-ins","type":false}],"postAnswer":"In what would be called the Children's Crusade, more than one thousand students skipped school on May 2 to meet at the 16th Street Baptist Church in Birmingham to join the demonstrations. More than six hundred marched out of the church fifty at a time in an attempt to walk to City Hall to speak to Birmingham's mayor about segregation. They were arrested and put into jail. On the next day, however, another one thousand students gathered at the church. When Bevel started them marching fifty at a time, Bull Connor finally unleashed police dogs on them and then turned the city's fire hoses water streams on the children. National television networks broadcast the scenes and widespread public outrage led the Kennedy administration to intervene more forcefully in negotiations between the white business community and the SCLC.","wikiLink":"https://en.wikipedia.org/wiki/Children%27s_Crusade"},{"question":"During which organized march did Martin Luther King recite his historic \"I Have a Dream\" speech?","answers":[{"content":"March on Washington for Jobs and Freedom","type":true},{"content":"Selma to Montgomery Marches","type":false},{"content":"Bloody Sunday","type":false}],"postAnswer":"The March on Washington for Jobs and Freedom was held in Washington, D.C. on Wednesday, August 28, 1963. The purpose of the march was to advocate for the civil and economic rights of African Americans. At the march, Martin Luther King Jr., standing in front of the Lincoln Memorial, delivered his historic \"I Have a Dream\" speech in which he called for an end to racism. The march was organized by A. Philip Randolph and Bayard Rustin, who built an alliance of civil rights, labor, and religious organizations.","wikiLink":"https://en.wikipedia.org/wiki/March_on_Washington_for_Jobs_and_Freedom"},{"question":"Which 1965 effort, launched in the wake of the murder of Jimmie Lee Jackson, led to the passage of the Voting Rights Act?","answers":[{"content":"Selma to Montgomery Marches","type":true},{"content":"Alabama Voting Rights Project","type":false},{"content":"Northern Campaign","type":false}],"postAnswer":"The Selma to Montgomery marches were three protest marches, held in 1965, along the 54-mile (87 km) highway from Selma, Alabama, to the state capital of Montgomery. The marches were organized by nonviolent activists to demonstrate the desire of African-American citizens to exercise their constitutional right to vote, in defiance of segregationist repression; they were part of a broader voting rights movement underway in Selma and throughout the American South. By highlighting racial injustice, stemming from the murder of activist and deacon Jimmie Lee Jackson  by state trooper James Bonard Fowler, they contributed to passage that year of the Voting Rights Act, a landmark federal achievement of the civil rights movement.","wikiLink":"https://en.wikipedia.org/wiki/Selma_to_Montgomery_marches"}]
export default quizQuestions;
